import { RightOutlined, SmileTwoTone } from "@ant-design/icons";
import { Button, Collapse, Spin } from "antd";
import { Card, List, NavBar, WhiteSpace } from "antd-mobile";
import faker from "faker";
import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import API from "../data/API";

const Item = List.Item;

const { Panel } = Collapse;

const Account = (props) => {
  const [account, setAccount] = useState({});
  const [userEmail, setUserEmail] = useState("");
  const [accountLoaded, setaccountLoaded] = useState(false);
  const [activePanel, setActivePanel] = useState(undefined);

  const { id } = useParams();
  const route = `/account/${id}`;

  useEffect(() => {
    API.getAccount(id)
      .then((res) => {
        setAccount(res);
        setUserEmail(res.userEmail);
        setaccountLoaded(true);
      })
      .catch((err) => {
        console.log(err);
      });

    return () => {
      setAccount([]);
    };
  }, []);

  return (
    <>
      {accountLoaded ? (
        <Collapse
          accordion
          expandIconPosition="right"
          onChange={(e) => {
            console.log(e);
            setActivePanel(e);
          }}
        >
          {Object.keys(account.userEmails).map((k) => {
            const emails = account.userEmails[k].emails;
            const folder_id = account.userEmails[k]._id;
            const folderName = account.userEmails[k].name;

            return (
              <Panel
                header={folderName}
                forceRender={true}
                key={folder_id}
                style={{ padding: "0px" }}
              >
                {emails.map((email) => {
                  return (
                    <>
                      <WhiteSpace size="lg" />
                      <Card full key={email.id}>
                        <Card.Header
                          title={<h4>{email.title}</h4>}
                          thumb={faker.image.avatar()}
                          thumbStyle={{
                            width: "32px",
                            height: "32px",
                            borderRadius: "50%",
                          }}
                          extra={
                            <h6>{new Date(email.time).toLocaleTimeString()}</h6>
                          }
                        />
                        <Card.Body>
                          <div>{email.body}</div>
                        </Card.Body>
                      </Card>
                    </>
                  );
                })}
              </Panel>
            );
          })}
        </Collapse>
      ) : (
        <Spin />
      )}
    </>
  );
};
export default Account;
